
.o-wrapper{
  width:100%;
  height: calc(100vh - 1.5rem);
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.o-ul{
  list-style: none;
  margin:0;
  /*padding:.2rem;*/
}
.o-li{
  width: calc(100% - 0.0rem);
  height:1rem;
  padding: .1rem;
  border-bottom: 1px solid black;
  vertical-align: middle;
  line-height:1rem;
}
.fixed-item{
  position: fixed;
  left:0;
  top:0;
  background: #ccc;
}
.static-item{
  position: static;
  background: transparent;
}





